@import url(https://fonts.googleapis.com/css?family=Roboto:400,500,700&display=swap);
*{
    margin: 0;
    padding: 0;
    outline: 0;
    box-sizing: border-box;
}

body{
    font:400 14px Roboto, sans-serif;
    background: #f0f0f5;
    -webkit-font-smoothing: antialiased;
}

input, button, textarea {
    font: 400 18px Roboto, sans-serif;
}

button {
    cursor: pointer;
}

form input {
    width: 100%;
    height: 60px;
    color: #333;
    border: 1px solid #dcdce5;
    border-radius: 8px;
    padding: 0 24px;
}

.button {
    widows: 100%;
    height: 60px;
    background: #e02041;
    border: 0;
    border-radius: 8px;
    color:#FFF;
    font-weight: 700;
    margin-top: 16px;
    display: inline-block;
    text-align: center;
    text-decoration: none;
    font-size: 18px;
    line-height: 60px ;
}

.logon-container{
    width: 100%;
    max-width: 1120px;
    height: 100vh;
    margin: 0 auto;

    display: flex;
    align-items: center;
    justify-content: center; 
}

.logon-container section.form{
    width: 100;
    max-width: 350px;
    margin-right: 30px;
}

.logon-container section.form form {
    margin-top: 10px;
}

.logon-container section.form form h1{
    font-size: 32px;
    margin-bottom: 32px;
}
